import { render, useState, useEffect } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { Button, Spinner, SelectControl } from '@wordpress/components';
import '../css/queue.css';

const Queue = () => {
	const [jobs, setJobs] = useState([]);
	const [loading, setLoading] = useState(true);
	const [error, setError] = useState(null);
	const [filter, setFilter] = useState('all');
	const [autoRefresh, setAutoRefresh] = useState(true);

	useEffect(() => {
		loadJobs();

		if (autoRefresh) {
			const interval = setInterval(loadJobs, 5000); // Refresh every 5 seconds
			return () => clearInterval(interval);
		}
	}, [filter, autoRefresh]);

	const loadJobs = async () => {
		try {
			setLoading(true);
			const response = await apiFetch({
				path: `/ai-media/v1/jobs?status=${filter}&per_page=50`,
				method: 'GET',
			});
			setJobs(response.jobs || []);
			setError(null);
		} catch (err) {
			setError(err.message);
		} finally {
			setLoading(false);
		}
	};

	const getStatusBadge = (status) => {
		const statusClasses = {
			pending: 'status-pending',
			processing: 'status-processing',
			processed: 'status-processed',
			approved: 'status-approved',
			failed: 'status-failed',
		};

		const statusLabels = {
			pending: 'Pending',
			processing: 'Processing',
			processed: 'Processed',
			approved: 'Approved',
			failed: 'Failed',
		};

		return (
			<span className={`ai-media-status ${statusClasses[status] || ''}`}>
				{statusLabels[status] || status}
			</span>
		);
	};

	const formatDate = (dateString) => {
		if (!dateString) return 'N/A';
		const date = new Date(dateString);
		return date.toLocaleString();
	};

	if (error) {
		return (
			<div className="ai-media-queue-error">
				<p>Error loading queue: {error}</p>
				<Button onClick={loadJobs} variant="primary">
					Retry
				</Button>
			</div>
		);
	}

	return (
		<div className="ai-media-queue">
			<div className="ai-media-queue-header">
				<div className="ai-media-queue-filters">
					<SelectControl
						label="Filter by Status"
						value={filter}
						options={[
							{ label: 'All', value: 'all' },
							{ label: 'Pending', value: 'pending' },
							{ label: 'Processing', value: 'processing' },
							{ label: 'Processed', value: 'processed' },
							{ label: 'Approved', value: 'approved' },
							{ label: 'Failed', value: 'failed' },
						]}
						onChange={setFilter}
						style={{ minHeight: '38px' }}
					/>
				</div>

				<div className="ai-media-queue-actions">
					<label>
						<input
							type="checkbox"
							checked={autoRefresh}
							onChange={(e) => setAutoRefresh(e.target.checked)}
						/>
						Auto-refresh (5s)
					</label>

					<Button
						variant="secondary"
						onClick={loadJobs}
						disabled={loading}
					>
						<span className="dashicons dashicons-update"></span> Refresh
					</Button>
				</div>
			</div>

			{loading && jobs.length === 0 ? (
				<div className="ai-media-queue-loading">
					<Spinner />
					<p>Loading queue...</p>
				</div>
			) : (
				<div className="ai-media-queue-table">
					<table className="wp-list-table widefat fixed striped">
						<thead>
							<tr>
								<th>ID</th>
								<th>Attachment</th>
								<th>Language</th>
								<th>Provider</th>
								<th>Status</th>
								<th>Score</th>
								<th>Created</th>
								<th>Processed</th>
							</tr>
						</thead>
						<tbody>
							{jobs.length === 0 ? (
								<tr>
									<td colSpan="8" style={{ textAlign: 'center', padding: '2em' }}>
										No jobs found.
									</td>
								</tr>
							) : (
								jobs.map((job) => (
									<tr key={job.id}>
										<td>{job.id}</td>
										<td>
											<strong>#{job.attachment_id}</strong>
											{job.error_message && (
												<div className="error-message" style={{ color: '#dc3232', fontSize: '12px' }}>
													{job.error_message}
												</div>
											)}
										</td>
										<td>{job.language_code || 'N/A'}</td>
										<td>
											{job.provider || 'N/A'}
											{job.model && (
												<div style={{ fontSize: '11px', color: '#666' }}>{job.model}</div>
											)}
										</td>
										<td>{getStatusBadge(job.status)}</td>
										<td>
											{job.score ? (
												<span className={job.score >= 0.85 ? 'score-high' : 'score-medium'}>
													{(job.score * 100).toFixed(0)}%
												</span>
											) : (
												'N/A'
											)}
										</td>
										<td>{formatDate(job.created_at)}</td>
										<td>{formatDate(job.processed_at)}</td>
									</tr>
								))
							)}
						</tbody>
					</table>
				</div>
			)}

			<div className="ai-media-queue-footer">
				<p>
					Showing {jobs.length} job{jobs.length !== 1 ? 's' : ''}
					{loading && <Spinner style={{ marginLeft: '10px' }} />}
				</p>
			</div>
		</div>
	);
};

// Initialize queue when DOM is ready
document.addEventListener('DOMContentLoaded', () => {
	const root = document.getElementById('ai-media-queue-root');
	if (root) {
		render(<Queue />, root);
	}
});
